.Home_tile__1XTbK {
    width: 300px;
    height: 200px;
    position: relative;
    margin-bottom: 50px;
    margin-left: 50px;
    overflow: hidden;
    -webkit-transition: all 0.15s;
    transition: all 0.15s;

    box-shadow: 0 5px 10px 0 rgba(0,0,0,.26);

}
.Home_tile__1XTbK:hover {
    box-shadow: none;
}
.Home_tile__1XTbK:before {
    content: '';
    -webkit-transition: all 0.15s;
    transition: all 0.15s;

    position: absolute;
    background:rgba(0,0,0,0.15);
    left: 0;
    top: 0;
    width: 100%;
    height: 350px;
    -webkit-transform: translateY(80px);
            transform: translateY(80px);
    background:-webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,0)),color-stop(15%, rgba(0,0,0,0)),color-stop(45%, rgba(45,46,54,.71)),to(rgba(45,46,54,.94)));
    background:linear-gradient(to bottom,rgba(0,0,0,0) 0%,rgba(0,0,0,0) 15%,rgba(45,46,54,.71) 45%,rgba(45,46,54,.94) 100%);


}
.Home_tile__1XTbK:hover.Home_tile__1XTbK:before {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

